.about-background {
    background-color: #4b4b4b;
    padding: 20px;
    overflow: auto;
    max-height: calc(100vh - 60px); 
    margin-bottom: 30px;
}

.about-content {
    margin: 2vw;
    max-width: 1200px; 
    margin: 0 auto; 
}

.about-content h2 {
    font-size: 3rem;
    font-weight: bold;
    margin-bottom: 0.5rem;
    color: #bbed2f; 
    text-align: left;
    margin-bottom: 0.5rem;
}

.about-content .intro p,
.about-content .education p,
.about-content .skills ul,
.about-content .education ul {
    font-size: 2rem;
    color: white;
    text-align: justify;
    margin-bottom: 0.8rem;
}

.about-content .intro h3,
.about-content .skills h3,
.about-content .education h3 {
    font-size: 3rem; 
    font-weight: bold;
    margin-bottom: 1rem;
    color: #bbed2f; 
    text-align: left;
}

.education {
    margin-bottom: 40px;
}


@media (max-width: 50em) {
    .about-background {
      background-color: #4b4b4b;
      padding: 10px; 
      overflow: auto;
      max-height: calc(100vh - 60px);
    }
  
    .about-content {
      margin: 10px 0; 
    }
  
    .about-content h2 {
      font-size: 1rem; 
      font-weight: bold;
      margin-bottom: 1rem;
      color: #bbed2f;
      text-align: left;
    }
  
    .about-content p {
      font-size: 0.8rem; 
      color: white;
      text-align: justify;
      margin-bottom: 1rem;
    }
  
    .about-content h3 {
      font-size: 1rem; 
      font-weight: bold;
      margin-bottom: 1rem;
      color: #bbed2f;
      text-align: left;
    }
  }
  